class quickfind {
  constructor(n) {
    this.array = this.initializeArray(n)
    this.sizeArray = this.initializeSizeArray(n)
  }

  initializeArray(n) {
    var array = []
    for (var i = 0; i < n; i++) {
      array.push(i)
    }
    return array
  }

  initializeSizeArray(n) {
    var array = []
    for (var i = 0; i < n; i++) {
      array.push(1)
    }
    return array
  }

  root(r) {
    while (this.array[r] != r) {
      this.array[r] = this.array[this.array[r]]
      r = this.array[r]
    }
    return r
  }

  connected(a, b) {
    return this.root(a) == this.root(b)
  }

  union(a, b) {
    var aRoot = this.root(a)
    var bRoot = this.root(b)
    if (aRoot !== bRoot) {
      if (this.sizeArray[bRoot] > this.sizeArray[aRoot]) {
        this.array[aRoot] = bRoot
        this.sizeArray[bRoot] += this.sizeArray[aRoot]
      } else {
        this.array[bRoot] = aRoot
        this.sizeArray[aRoot] += this.sizeArray[bRoot]
      }
    }
  }
}

module.exports = quickfind
