import React from "react"
import styled from "styled-components"
import QF from "../components/quickfind"

const rowConstruct = function(n) {
  var result = []
  for (var i = 0; i < n; i++) {
    result.push(<span>[x]</span>)
  }
  return result
}

const boardConstruct = function(n) {
  var result = []
  for (var i = 0; i < n; i++) {
    result.push(<div>rowConstruct(n)</div>)
  }
  return result
}

const quicksearch = () => {
  const Section = styled.div`
    display: flex;
    flex-direction: row;
    @media (max-width: 820px) {
      margin-left: 1em;
      margin-right: 1em;
    }
  `

  const search = new QF(20)

  return <Section>{boardConstruct(20)}</Section>
}

export default quicksearch
